@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;700;900&display=swap')

$baseColor: #FFFBEB
$bgColor: #263159
$subColor: #495579

.About
    text-align: center
    background:
        color: $bgColor
    height: '100vh'
.Header
    background-color: $subColor
    min-height: 10vh
    display: flex
    flex-direction: column
    align-items: left
    justify-content: center
    font:
        family: 'Noto Sans JP'
        size: calc(60px + 1vmin)
        weight: 900
    color: white
    .Icon
        color: $baseColor
        position: absolute
        margin:
            top: 46px
.Main
    background-color: $baseColor
    min-height: 80vh
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    font:
        family: 'Noto Sans JP'
        size: calc(20px + 2vmin)
        weight: 900
.Menu
    text-align: right
    align-items: center
    color: white
    padding-right: 10px
    min-height: 5vh
    display: flex
    flex-direction: row-reverse
.SubFooter
    background-color: $subColor
    min-height: 0.5vh
.Footer
    color: white
    min-height: 4.5vh
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    font:
        family: 'Noto Sans JP'
        size: calc(4px + 1vmin)
        weight: 100
